import { h, Component } from 'preact';
import style from './about';

export default class About extends Component {
	render() {
		return (
			<div class={style.about}>
        <div class={'page-header ' + style.header}>Our Story</div>

				<p>Codingscape is a software team that has been working with front and back end web solutions for over 20 years for companies large and small, including Zappos, Amazon, Overstock.com and Backcountry.  We will help you execute on your roadmap either directly by working with your engineers and product teams or by taking on projects for you.</p>

				<p>There is another paragraph here. It is pretty good and inspiring, but it's also thought provoking. You wonder what the author was thinking when they wrote this paragraph, and whether perhaps they are a little bit like you.</p>

				<h2>Founders</h2>

				<div class={style.founder}>
					<div class={style.founderimage}>
						<img src="assets/images/porter.jpg" />
					</div>
					<div class={style.foundertext}>
						<b>Porter Haney</b>
						<p>Porter wrangles the business side of Codingscape. When he's not building double decker nachos or skiing powder, he's crunching numbers and helping connect great people with one another. If you're willing to trade stories with him, you might hear something juicy. 👍</p>
					</div>
				</div>

				<div class={style.founder}>
					<div class={style.founderimage}>
						<img src="assets/images/jimmy.jpg" />
					</div>
					<div class={style.foundertext}>
						<b>Jimmy Jacobson</b>
						<p>It'd be hard to help raise little girls if they looked like lines of code. Luckily, Jimmy is half man, half machine. When he's not coding, he's deciding which technologies will best serve our clients. Pwning newbs at COD or rocking headstands in Yoga class, it makes no difference to him.</p>
					</div>
				</div>


				<div class={style.founder}>
					<div class={style.founderimage}>
						<img src="assets/images/pawel.jpg" />
					</div>
					<div class={style.foundertext}>
						<b>Pawel Szymczykowski</b>
						<p>Pawel likes to make things. Sometimes they are made of wood or PLA, sometimes electronic components, and other times they are made out of code. He is an architect and cofounder at Codingscape and does some other useful things too. He has a Shiba Inu named 🍄.</p>
					</div>
				</div>


			</div>
		);
	}
}



